/* 
REGION UTILITY
Consistent block padding for page sections.
*/
.region-small {
  padding-block: var(--region-space-small, var(--space-m-l));
}

.region-normal {
  padding-block: var(--region-space-normal, var(--space-xl-2xl));
}

.region-large {
  padding-block: var(--region-space, var(--space-2xl-3xl));
}

.region-bottom {
  padding-block-end: var(--region-space, var(--space-xl-2xl));
}

/* 
FLOW UTILITY
Spacing value between content blocks.
*/
:where(.flow-tiny) > :where(* + *) {
  margin-block-start: var(--flow-space-tiny, 0.5em);
}

:where(.flow-small) > :where(* + *) {
  margin-block-start: var(--flow-space-small, 0.5em);
}

:where(.flow) > :where(* + *) {
  margin-block-start: var(--flow-space, 1em);
}

:where(.flow-large) > :where(* + *) {
  margin-block-start: var(--flow-space-large, 2em);
}

:where(.flow-page-regions) > :where(* + *) {
  margin-block-start: var(--flow-space-xl, 8em);
}

/* 
WRAPPER UTILITY
Settings for the overall content width.
*/
.wrapper {
  position: relative;
  max-width: 87.5rem;
  margin-inline: auto;
  padding-inline: var(--gutter);
}
@media (min-width: 37.5rem) {
  .wrapper {
    padding-inline: var(--gutter-large);
  }
}

/* 
VISUALLY HIDEN UTILITY
Modern way of properly hiding elements.
*/
.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 0;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

/* 
BORDER RADIUS UTILITY
Values for consistent border radius.
*/
:root {
  --radius-xs: 6px;
  --radius-small: 8px;
  --radius-normal: 16px;
  --radius-large: 48px;
  --radius-circle: 9999px ;
}

.rounded-small {
  border-radius: var(--radius-small);
}

.rounded {
  border-radius: var(--radius-normal);
}

.rounded-xs {
  border-radius: var(--radius-xs);
}

/* 
ICON TEXT GROUPS UTILITY
Groupings for text that have either leading or ttailing icons.
*/
:is(p, h1, h2, h3, h4, h5, h6).icon-text-group {
  display: flex;
  align-items: center;
  gap: var(--space-2xs);
}
:is(p, h1, h2, h3, h4, h5, h6).icon-text-group svg {
  fill: currentColor;
}

/* 
BG BLUR UTILITY
Applies background colour and blur to an element.
** Remeber to add position: relative to the element.**
*/
.bg-blur::before {
  content: "";
  position: absolute;
  inset: 0;
  width: 100%;
  backdrop-filter: blur(10px);
  background-color: rgba(0, 0, 0, 0.2);
  z-index: -1;
}

/* 
VARIOUS FLEX LAYOUT CLASSES
*/
.flex {
  display: flex;
  align-items: center;
  gap: var(--space-2xs);
}

.flex-splitter {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.flex-stack {
  display: flex;
  flex-direction: column;
  gap: var(--space-3xs);
}

.flex-push-right {
  justify-content: flex-end;
}

/* 
IMAGE BORDER HIGHLIGHT
Top left border radius effect.
*/
/**
Notes:
The only variable value in this mixin is '$highlight-color' and is used to change the highlighted colour from it's default value of --clr-surface-300.
Example usage with a different colour variable:
img::before {
    @include img-border-highlight(var(--clr-brand-accent));
}
**/
/**
Notes:
The '$end-color' should remain at --clr-surface-300 as it will blend with the site's main background colour.
Based on this, passing one custom colour will only change the highlight colour as intended.
Example usage for a custom start colour:
.element::before {
    @include panel-border-highlight(#ff6600);
}

If the section this is being applied to is on anything other than the site's main background colour, two values
can be passed and '$end-color' will be the second/last colour to match whatever background it sits on.
Example usage for a custom start colour:
.element::before {
    @include panel-border-highlight(#ffcc00, #ff6600);
}
**/
/* 
IMAGE STATE STYLES
Fancy css filter effects for image link hovers.
*/
.img-container {
  width: 100%;
  aspect-ratio: 16/9;
  border-radius: var(--radius-small);
  overflow: hidden;
}

.img-hover {
  filter: saturate(1) brightness(1);
  transition: filter 0.4s cubic-bezier(0.4, 0, 0.2, 1), transform 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  will-change: transform, filter;
  transform-origin: center center;
}
.img-hover:hover {
  filter: saturate(1.1) brightness(1.15);
  transform: scale(1.03);
}

