/* 
ICONS
Styling for Lucide Icons
*/
[data-lucide] {
  display: inline-block;
  vertical-align: middle;
  width: 1em;
  height: 1em;
  font-size: inherit;
  line-height: 1;
  stroke: currentColor;
  fill: none;
  stroke-width: 2;
  shape-rendering: geometricPrecision;
}
[data-lucide][aria-hidden=true] {
  display: none;
}

